/**/

const days = {
    english: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    indonesian: ['Minggu', 'Senin', 'Selasa', 'Rabu', 'Kamis', "Jum'at", 'Sabtu']
};

const months = [
    'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September',
    'October', 'November', 'December'];

function getDateTime() {

    let date = new Date;
    let index = {day: date.getDay(), month: date.getMonth()};
    let clock = ['getHours', 'getMinutes', 'getSeconds'].map (
        (func) => ('0').concat (date [func]()).substr (-2)).join (':');

    let result = [date.getDate(), months [index.month], date.getFullYear()].join (' ');
    result = days.english [index.day] + ('-') + [result, clock, 'WIB'].join (' ');
    
	return result;
}

export default getDateTime;
