import React, {useEffect, useState} from 'react';
import getDateTime from '../assets/js/updateTime';

function Topbar() {

    const [localTime, setLocalTime] = useState (getDateTime);
    const topbarStyle = {opacity: "0.9", backgroundColor: "#1a1a1a"}

    const timer = () => setLocalTime (getDateTime())
    useEffect (() => {setInterval (timer, 1000)}, [])

    /* new Date().toLocalTimeString() */

    return (
        <div>

            {/* <div className="d-flex flex-column align-items-end py-1" style={topbarStyle}>
                <p className="text-white font-weight-bold small float-right my-0 pr-3">{localTime}</p>
            </div> */}

            {/* <!-- Topbar --> */}
            <nav className="navbar navbar-expand navbar-light bg-white topbar mb-3 static-top shadow">

                {/* <!-- Topbar Search --> */}
                <form className="navbar-search d-none d-sm-inline-block form-inline mx-2 mr-auto my-2">
                    <div className="input-group">
                        <input type="text" className="form-control bg-light border small" aria-label="Search" aria-describedby="basic-addon2" />
                        <div className="input-group-append">
                            <button className="btn btn-warning border" type="button">
                                <i className="fas fa-search fa-sm"></i>
                            </button>
                        </div>
                    </div>
                </form>

                {/* <!-- Topbar Navbar --> */}
                <ul className="navbar-nav ml-auto">

                    <li class="nav-item dropdown no-arrow mx-2">
                        <a class="nav-link dropdown-toggle" href="#" id="alertsdd" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <i class="fas fa-fw fa-bell text-midnight"></i>
                            <span class="badge badge-danger badge-counter">36</span>
                        </a>
                    </li>

                    <div className="topbar-divider d-none d-sm-block"></div>

                    {/* <!-- Nav Item - User Information --> */}
                    <li className="nav-item dropdown justify-content-center align-items-center">
                        <div className="nav-link" href="#" id="userdd" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">

                            <a className="d-inline h1 img-profile text-midnight mr-4">
                                <i className="fas fa-user-circle"></i>
                            </a>

                            <div className="nav-item d-inline">
                                <span className="d-block text-danger text-xs">Welcome!</span>
                                <a className="d-block dropdown-toggle text-dark font-weight-bold small" href="#">
                                    Amirul Ihsan</a>
                            </div>
                            
                        </div>

                        {/* <!-- Dropdown - User Information --> */}
                        <div className="dropdown-menu dropdown-menu-right shadow animated--grow-in" aria-labelledby="userdd">
                            <a className="dropdown-item" href="#">
                                <i className="fas fa-fw fa-user mr-2 text-midnight"></i>Profile
                            </a>
                            <a className="dropdown-item" href="#">
                                <i className="fas fa-fw fa-cogs mr-2 text-midnight"></i>Account Settings
                            </a>
                            <a className="dropdown-item" href="#">
                                <i className="fas fa-fw fa-list mr-2 text-midnight"></i>Activity Log
                            </a>
                            <div className="dropdown-divider"></div>
                            <a className="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal">
                                <i className="fas fa-fw fa-sign-out-alt mr-2 text-midnight"></i>Logout
                            </a>
                        </div>

                    </li>
                </ul>

            </nav>
            {/* <!-- End of Topbar --> */}
        </div>
    )
}

export default Topbar;